<template>
  <a href="javascript:;">
    <div class="item-poster" v-bind:style="img"></div>
    <span class="item-title">{{title}}</span>
    <div class="item-rating">
      <div class="rank">
        <div v-if="rating">
          <span class="rating-stars">
            <span class="rating-star" v-for="item in stars()" :key="item.id" v-bind:style="item"></span>
          </span>
          <span>{{ratingValue()}}</span>
        </div>
        <span v-else-if="data.price">{{"¥ " + data.price}}</span>
        <span v-else>暂无评分</span>
      </div>

    </div>
  </a>
</template>

<script>
  export default {
    name: 'item',
    props: ['data'],
    data() {
      return {
        img: {
          backgroundImage: 'url(' + this.data.cover.url + ')'
        },
        title: this.data.title,
        rating: this.data.rating,
        full: {
          backgroundImage: 'url(data:image/png;base64,)'
        },
        gray: {
          backgroundImage: 'url(data:image/png;base64,)'
        }

      }
    },
    methods: {
      stars() {
        let arr = []
        let f = this.rating.value / 2 - 1
        for (let i = 0; i <= f; i++) {
          arr[i] = this.full
        }
        for (let i = 4; i > f; i--) {
          arr[i] = this.gray
        }
        return arr
      },
      ratingValue() {
        if(this.rating.value%1 == 0) {
          return this.rating.value+".0"
        }else{
          return this.rating.value
        }
      }

    },
    mounted() {
    //   console.log(this.data)
    }
  }

</script>

<style scoped>
  .item-poster {
    width: 100%;
    overflow: hidden;
    background-size: cover;
    background-position: center;
  }

  .item-poster::before {
    content: "";
    float: left;
    margin-top: 142.8%;
  }

  .item-title {
    display: block;
    color: #111;
    margin-top: 20px;
    line-height: 36px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .item-rating {
    color: #aaa;
    line-height: 36px;
    margin-top: 12px;
    font-size: 28px;
  }

  .rating-stars {
    display: inline-block;
    vertical-align: center;
  }

  .rating-stars .rating-star {
    display: inline-block;
    margin-right: 1px;
    background-color: transparent;
    background-repeat: no-repeat;
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
  }

</style>



