<template>
    <div class="header">
        <router-link to="/"><h2 class="logo">豆瓣</h2></router-link>
        <nav>
            <ul>
                <li><router-link to="/movie"><a href="" style="color: #2384E8;">电影</a></router-link></li>
                <li><router-link to="/book"><a href="" style="color: #9F7860;">图书</a></router-link></li>
                <li><router-link to="/broadcast"><a href="" style="color: #E4A813;">广播</a></router-link></li>
                <li><router-link to="/group"><a href="" style="color: #2AB8CC;">小组</a></router-link></li>                
            </ul>
            <span></span>
        </nav>
    </div>
</template>

<script>
export default {
  
}
</script>

<style scoped>
    .header {
        width: 750px;
        border-bottom: 1px solid #f3f3f3;
        display: flex;
        align-items: center;
        height: 94px;
        padding: 0 36px;
        justify-content: space-between;
        position: fixed;
        background-color: #fff;
        z-index: 999;
    }
    .logo {
        background: url(data:image/png;base64,) no-repeat;
        color: #00b600;
        height: 44px;
        width: 92px;
        background-size: cover;
        display: inline-block;
        line-height: 64px;
        font-size: 0;
    }
    .header nav>* {
        display: inline-block;
        vertical-align: middle;
    }
    .header nav li {
        display: inline-block;
        font-size: 30px;
        margin-right:31px;
    }
    .header nav span {
        font-size: 0;
        background:url(data:image/png;base64,) no-repeat;
        background-size: cover;
        width: 49px;
        height: 36px;
        margin-top: 8px;
        position: relative;
    }

</style>



